'use client'
import { useState, useEffect } from 'react';
import img from '../../src/assets/image/pngwing 1.png';
import GenderOptions from '../components/gender';
import { io } from 'socket.io-client';
const socket = io('http://localhost:4000');
interface ModelGenderOptions {
    id: number;
    title: string;
    gender: null | string;
    genderSelection: string[]
}
const Main = () => {
    const [room, setRoom] = useState<{ active: boolean, room: string }[]>([])
    const initiateSearch = () => {
        socket.emit('createRoom', 'damir');
    }
    useEffect(() => {
        // Устанавливаем слушатель
        socket.on('roomCreated', (r) => {
            console.log('Комната создана:', r);
            // Здесь вы можете обновить состояние, если нужно
            setRoom(r);
        });

        // Очистка слушателя при размонтировании компонента
        return () => {
            socket.off('roomCreated');
        };
    }, []);

    console.log(room);
    
    const [genderOptions, setGenderOptions] = useState<ModelGenderOptions[]>([
        {
            id: 1,
            title: 'Ваш пол',
            gender: null,
            genderSelection: ['М', 'Ж']

        },
        {
            id: 2,
            title: 'Пол собеседника',
            gender: null,
            genderSelection: ['М', 'Ж']
        },
    ])

    // const hangleClick = (id, gender) => {
    //     setGenderOptions(genderOptions.map((option) =>
    //         option.id === id ? { ...option, gender: gender } : { ...option }))
    // }

    return (
        <>
            <main className="main-content">
                <div className="container">
                    <div className="intro-section-container">
                        <div className="intro-section">
                            <h1>В нашем мире анонимных знакомств каждое лицо - загадка, каждый человек - история.</h1>
                            <span>Помните, ответственность за вашу безопасность лежит на вас. Не передавайте личные данные и будьте осторожны.</span>
                        </div>
                        {/* <img className="main-image" src={img} alt="" /> */}
                    </div>
                    <form className="search-form" action="">
                        <div className="form-section">
                            {genderOptions.map(({ id, title, genderSelection }) => <GenderOptions id={id} title={title} genderSelection={genderSelection} />)}
                        </div>
                        <input onClick={() => initiateSearch()} className="search-button" type="button" value="Поиск" />
                    </form>
                </div>
            </main>

        </>
    )
}
export default Main;